export const createHabit = (habit) => {
    return dispatch => {
        axios.post('/habits.json', habit)
            .then(({data})=>console.log(data))
    }
}

export const removeHabit = (title) => ({
    type: 'REMOVE_HABIT',
    title
})

export const resetHabit = (title) => ({
    type: 'RESET_HABIT',
    title
})

export const setReducerState = (payload) => ({
    type: 'SET_STATE',
    payload
})


